import { actionCreator } from '../../utils/generators';
import { formatDateTime } from '../../utils/DateUtils';
import AccountsActionTypes from './accounts.types';
import produce from 'immer';
import FormatsConfiguration from '../../utils/FormatsConfiguration';

const defaultState = {
  isLoading: false,
  error: null,
  accounts: [],
  signaletic: {
    fullName: '',
    title: '',
    branch: { bank: { name: '' } },
    legalNumber: '',
    mobileNumber: '',
    address: '',
    birthday: '',
  },
  selectedAccountId: '',
  modalShow: false,
  activeTab: 'operations',
};

const accountsReducer = produce((draft, action) => {
  switch (action.type) {
    case actionCreator('req', AccountsActionTypes.GET_ACCOUNTS):
    case actionCreator('req', AccountsActionTypes.GET_SIGNALETIC):
      draft.error = null;
      draft.loading = true;
      break;
    case actionCreator('res', AccountsActionTypes.GET_ACCOUNTS):
      draft.error = null;
      draft.loading = false;
      draft.accounts = action.payload.data;
      if (draft.accounts && draft.accounts.length) {
        draft.selectedAccountId = draft.accounts[0].clientAccountId;
      }
      break;
    case actionCreator('fail', AccountsActionTypes.GET_ACCOUNTS):
    case actionCreator('fail', AccountsActionTypes.GET_SIGNALETIC):
      draft.error = 'error found';
      draft.loading = false;
      break;
    case actionCreator('req', AccountsActionTypes.SELECT_ACCOUNT):
      draft.selectedAccountId = parseInt(action.payload);
      break;
    case actionCreator('res', AccountsActionTypes.GET_SIGNALETIC):
      draft.error = null;
      draft.loading = false;
      let signaletic = action.payload.data;
      if (signaletic.birthday) {
        signaletic.birthday = formatDateTime(
          signaletic.birthday,
          FormatsConfiguration.DateTimeFormat.fullDate,
        );
      }
      signaletic.fullName = `${signaletic.title} ${signaletic.firstName} ${signaletic.lastName}`;
      draft.signaletic = signaletic;
      break;
    case actionCreator('req', AccountsActionTypes.TOGGLE_MODAL):
      draft.modalShow = !draft.modalShow;
      break;
    default:
      break;
  }
}, defaultState);
export default accountsReducer;
