import { actionCreator } from '../../utils/generators';
import CardsActionTypes from './cards.types';
import produce from 'immer';
import { formatDateTime } from '../../utils/DateUtils';
import FormatsConfiguration from '../../utils/FormatsConfiguration';

const defaultState = {
  isLoading: false,
  error: null,
  cards: [],
  cardshistory: [],
  selectedCardNumber: '',
  activeTab: 'cardsDetails',
};

const cardsReducer = produce((draft, action) => {
  switch (action.type) {
    case actionCreator('req', CardsActionTypes.GET_CARDS):
      draft.error = null;
      draft.loading = true;
      break;
    case actionCreator('res', CardsActionTypes.GET_CARDS):
      draft.error = null;
      draft.loading = false;
      draft.cards = action.payload.data;
      draft.cards = draft.cards.map(cardElement => ({
        ...cardElement,
        activeState: cardElement.activationFlag === 'Y',
        cardImg: cardElement.nomProduit.replace(/\s+/g, ''),
        ownerName: `${cardElement.familyName} ${cardElement.firstName}`,
        formatedExpirationDate: formatDateTime(
          cardElement.expiryDate,
          FormatsConfiguration.DateTimeFormat.expDate,
        ),
        productCode: cardElement.produitCarte,
        oppositionState: cardElement.flagOpposition === 'Y',
      }));
      break;
    case actionCreator('fail', CardsActionTypes.GET_CARDS):
      draft.error = 'error found';
      draft.loading = false;
      break;
    case actionCreator('req', CardsActionTypes.SELECT_CARD):
      draft.selectedCardNumber = action.payload;
      break;
    case actionCreator('req', CardsActionTypes.ACTIVATE_TAB):
      draft.activeTab = action.payload;
      break;
    case actionCreator('req', CardsActionTypes.GET_HISTORY):
      draft.error = null;
      draft.loading = true;
      break;
    case actionCreator('res', CardsActionTypes.GET_HISTORY):
      let operations = action.payload.data;
      draft.operations = operations.slice();
      draft.error = null;
      draft.isLoading = false;
      break;
    case actionCreator('fail', CardsActionTypes.GET_HISTORY):
      draft.error = 'error found';
      draft.loading = false;
      break;
    case actionCreator('req', CardsActionTypes.ACTIVATE):
    case actionCreator('req', CardsActionTypes.DEACTIVATE):
    case actionCreator('req', CardsActionTypes.OPPOSE):
      draft.error = null;
      draft.loading = true;
      break;
    case actionCreator('res', CardsActionTypes.ACTIVATE):
    case actionCreator('res', CardsActionTypes.DEACTIVATE):
      let changedCard = action.payload.data.numberCard;
      draft.cards.forEach((card, index) => {
        if (card.cardNumber === changedCard) {
          draft.cards[index].activeState = !draft.cards[index].activeState;
        }
      });
      /*let cardNumber = action.payload.data.cardNumber;
      let selectedCard = draft.cards.filter(
        card => card.cardNumber === cardNumber,
      )[0];
      selectedCard.activationFlag =
        selectedCard.activationFlag === 'Y' ? 'N' : 'Y';
      selectedCard.activeState = selectedCard.activationFlag === 'Y';*/
      break;

    case actionCreator('res', CardsActionTypes.OPPOSE):
      let opposedCard = action.payload.data.numberCard;
      draft.cards.forEach((card, index) => {
        if (card.cardNumber === opposedCard) {
          draft.cards[index].oppositionState = false;
        }
      });
      draft.error = null;
      draft.isLoading = false;
      break;
    case actionCreator('fail', CardsActionTypes.ACTIVATE):
    case actionCreator('fail', CardsActionTypes.DEACTIVATE):
    case actionCreator('fail', CardsActionTypes.OPPOSE):
      draft.error = 'error found';
      draft.loading = false;
      break;
    default:
      break;
  }
}, defaultState);
export default cardsReducer;
