import { put, takeLatest, call, all, select } from 'redux-saga/effects';
import Cards from '../../services/apis/Cards';
import { actionCreator } from '../../utils/generators';
import types from './cards.types';
import { getSelectedCardNumber } from './cards.selectors';
import { sagaWithLoader } from '../template/sagaWithLoader';

function* getCards(action) {
  try {
    const cards = yield call(new Cards().getCards);
    yield put({
      type: actionCreator('res', types.GET_CARDS),
      payload: cards,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_CARDS),
      }),
    ]);
  }
}
function* getCardHistory(action) {
  try {
    const cardHistory = yield call(
      new Cards().getCardHistory,
      yield select(getSelectedCardNumber),
    );
    yield put({
      type: actionCreator('res', types.GET_HISTORY),
      payload: cardHistory,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_HISTORY),
      }),
    ]);
  }
}
function* activate(action) {
  try {
    const response = yield call(
      new Cards().activateCard,
      yield select(getSelectedCardNumber),
    );
    yield put({
      type: actionCreator('res', types.ACTIVATE),
      payload: response,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.ACTIVATE),
      }),
    ]);
  }
}
function* deactivate(action) {
  try {
    const response = yield call(
      new Cards().deactivateCard,
      yield select(getSelectedCardNumber),
    );
    yield put({
      type: actionCreator('res', types.DEACTIVATE),
      payload: response,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.DEACTIVATE),
      }),
    ]);
  }
}
function* oppose(action) {
  try {
    const response = yield call(
      new Cards().opposeCard,
      yield select(getSelectedCardNumber),
    );
    yield put({
      type: actionCreator('res', types.OPPOSE),
      payload: response,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.OPPOSE),
      }),
    ]);
  }
}

export function* cardsSagas() {
  yield takeLatest(
    actionCreator('req', types.GET_CARDS),
    sagaWithLoader,
    getCards,
  );
  yield takeLatest(actionCreator('req', types.GET_HISTORY), getCardHistory);
  yield takeLatest(
    actionCreator('req', types.ACTIVATE),
    sagaWithLoader,
    activate,
  );
  yield takeLatest(
    actionCreator('req', types.DEACTIVATE),
    sagaWithLoader,
    deactivate,
  );
  yield takeLatest(actionCreator('req', types.OPPOSE), sagaWithLoader, oppose);
}
