export default class FormatsConfiguration {
  static NumberFormat = {
    decimals: 2,
    groupsSeparator: ' ',
    groupingBlock: 3,
  };
  static NumberExcelFormat = {
    decimals: 2,
    groupsSeparator: ',',
    groupingBlock: 3,
  };

  static AmountFormat = {
    ...FormatsConfiguration.NumberFormat,
    decimals: 0,
    currencyIsPrefix: false,
  };
  static AmountExcelFormat = {
    ...FormatsConfiguration.NumberExcelFormat,
    decimals: 0,
    currencyIsPrefix: false,
  };
  static DateTimeFormat = {
    shortDate: 'MM/YYYY',
    fullDate: 'DD/MM/YYYY',
    time: 'HH:MM:SS',
    dateTime: 'DD/MM/YYYY, HH:MM:SS',
    expDate: 'MM/YY',
  };
}
