import moment from 'moment';
import FormatsConfiguration from './FormatsConfiguration';
export const formatDateTime = (dateTime, format) => {
  if (!dateTime) return null;
  return moment(dateTime).format(format);
};

export const dateFilter = (value, filter) => {
  value = formatDateTime(value, FormatsConfiguration.DateTimeFormat.fullDate);
  let filters = filter.map(filterElement => {
    filterElement = formatDateTime(
      filterElement,
      FormatsConfiguration.DateTimeFormat.fullDate,
    );
    return filterElement;
  });
  return filters.some(search => value.startsWith(search));
};
